import * as React from "react";

export function Select({
    value,
    onValueChange,
    children
}: {
    value: string;
    onValueChange: (val: string) => void;
    children: React.ReactNode;
}) {
  return <div className="relative">{children}</div>; // For integration, actual logic can go inside trigger/content
}

export function SelectTrigger({
    children,
    className = "",
    ...props
}: {
    children: React.ReactNode;
    className?: string;
    [key: string]: any;
}) {
    return (
    <button
        className={`flex w-full items-center justify-between rounded-xl border border-gray-300 bg-white px-3 py-2 text-sm dark:bg-zinc-900 ${className}`}
        {...props}
    >
        {children}
    </button>
        );
}

export function SelectContent({ children }: { children: React.ReactNode }) {
    return (
    <div className="absolute z-10 mt-1 w-full rounded-xl border border-gray-200 bg-white shadow-lg dark:bg-zinc-900">
        {children}
    </div>
    );
}

export function SelectItem({
    children,
    value,
    onClick,
}: {
    children: React.ReactNode;
    value: string;
    onClick?: (val: string) => void;
}) {
    return (
    <div
        className="cursor-pointer px-4 py-2 hover:bg-gray-100 dark:hover:bg-zinc-800"
        onClick={() => onClick?.(value)}
    >
        {children}
    </div>
    );
}

export function SelectValue({ placeholder }: { placeholder: string }) {
    return <span className="text-gray-500">{placeholder}</span>;
}
