import * as React from "react";

export const Input = React.forwardRef<HTMLInputElement, React.InputHTMLAttributes<HTMLInputElement>>(
    ({ className = "", ...props }, ref) => {
    return (
        <input
        ref={ref}
        className={`w-full rounded-xl border border-gray-300 dark:border-gray-700 px-3 py-2 text-sm focus:outline-none focus:ring focus:ring-indigo-500 dark:bg-zinc-900 ${className}`}
        {...props}
        />
    );
    }
);
Input.displayName = "Input";
