import React, { useState } from "react";
import { Input } from "./ui/input";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "./ui/select";
import { Button } from "./ui/button";
import { FilterIcon } from "lucide-react";

interface FilterProps {
  onFilterChange: (filters: { search: string; status: string }) => void;
}

const Filter: React.FC<FilterProps> = ({ onFilterChange }) => {
  const [search, setSearch] = useState("");
  const [status, setStatus] = useState("");

  const handleApply = () => {
    onFilterChange({ search, status });
  };

  const handleReset = () => {
    setSearch("");
    setStatus("");
    onFilterChange({ search: "", status: "" });
  };

  return (
    <div className="flex flex-col sm:flex-row gap-4 p-4 items-center justify-between border-b">
      <div className="flex gap-2 w-full sm:w-1/2">
        <Input
          placeholder="Search PRs by title..."
          value={search}
          onChange={(e) => setSearch(e.target.value)}
        />

        <Select value={status} onValueChange={(value) => setStatus(value)}>
          <SelectTrigger className="w-40">
            <SelectValue placeholder="Status" />
          </SelectTrigger>
          <SelectContent>
            <SelectItem value="">All</SelectItem>
            <SelectItem value="open">Open</SelectItem>
            <SelectItem value="closed">Closed</SelectItem>
            <SelectItem value="merged">Merged</SelectItem>
          </SelectContent>
        </Select>
      </div>

      <div className="flex gap-2">
        <Button onClick={handleApply} variant="default">
          <FilterIcon className="mr-2 h-4 w-4" /> Apply
        </Button>
        <Button onClick={handleReset} variant="outline">
          Reset
        </Button>
      </div>
    </div>
  );
};

export default Filter;
